export default {
  name: 'goodslist',
  data () {
    return {
      tlink: '',
      copyBtn: null,
      show: false,
      copyText: '一键复制'
    }
  },
  props: ['goodslist', 'loadingstatus', 'arrangement'],
  methods: {
    /**
     * JS实现精确减法计算
     */
    sub (arg1, arg2) {
      var r1, r2, m, n
      try {
        r1 = arg1.toString().split('.')[1].length
      } catch (e) {
        r1 = 0
      }
      try {
        r2 = arg2.toString().split('.')[1].length
      } catch (e) {
        r2 = 0
      }
      m = Math.pow(10, Math.max(r1, r2))
      // last modify by deeka
      // 动态控制精度长度
      n = r1 >= r2 ? r1 : r2
      return ((arg1 * m - arg2 * m) / m).toFixed(n)
    },
    /**
     * 获得淘口令
     */
    getCoupon (goods) {
    	var that=this
    	uni.request({
    		url: getApp().globalData.APIurl+'/portal/index/sortMaterialUrl',
    		data: {
    		    url: goods.click_url,
    		    title: goods.title,
    		    img_url: goods.pict_url
    		},
    		header: {
    		    'Token': that.token ,
    			'Device-Type':'wxapp'
    		},
    		success: (res) => {
    			if(res.data.code){
    				that.tlink = res.data.data.info
    				uni.setClipboardData({
    				    data: that.tlink,
    				    success: function () {
    				        uni.showToast({
    				            title: '复制成功，打开【手机淘宝APP】即可【领取优惠券】并购买',
    				        	icon:'none',
    				            duration: 2000,
    				        	success:function(){
    				        		
    				        	}
    				        });
    				    }
    				});
    			}
    		}
    	})
    }
  }
}
